"use client";

import { useState, useEffect, useCallback } from "react";
import { Inter } from "next/font/google";

// Load Inter font with specific weights
const inter = Inter({
  subsets: ["latin"],
  weight: ["500", "700"],
  variable: "--font-inter",
});

/**
 * NOW Mode - A tool that replaces compulsive clock-checking with deliberate choice and presence.
 *
 * Default view: Centered large "NOW" text in yellow.
 * Tap/click anywhere: Fade transition to show current time.
 * Second tap: Fade back to "NOW".
 */
export default function Home() {
  const [showingTime, setShowingTime] = useState(false);
  const [timeString, setTimeString] = useState("--:--");

  /**
   * Format current time in HH:MM format
   */
  const updateTime = useCallback(() => {
    const now = new Date();
    const hours = String(now.getHours()).padStart(2, "0");
    const minutes = String(now.getMinutes()).padStart(2, "0");
    setTimeString(`${hours}:${minutes}`);
  }, []);

  /**
   * Toggle between NOW and time display
   * Updates time before showing to ensure freshness
   */
  const toggleDisplay = useCallback(() => {
    if (!showingTime) {
      updateTime();
    }
    setShowingTime((prev) => !prev);
  }, [showingTime, updateTime]);

  /**
   * Handle keyboard interaction (Space or Enter)
   */
  const handleKeyDown = useCallback(
    (e: React.KeyboardEvent) => {
      if (e.key === " " || e.key === "Enter") {
        e.preventDefault();
        toggleDisplay();
      }
    },
    [toggleDisplay],
  );

  // Update time every minute when visible
  useEffect(() => {
    if (!showingTime) return;

    updateTime();
    const interval = setInterval(updateTime, 60000);

    return () => clearInterval(interval);
  }, [showingTime, updateTime]);

  return (
    <main
      className={`${inter.variable} font-sans h-screen w-screen bg-black flex items-center justify-center overflow-hidden select-none`}
    >
      {/* Full-screen clickable container */}
      <div
        role="button"
        aria-label="Toggle time display"
        tabIndex={0}
        onClick={toggleDisplay}
        onKeyDown={handleKeyDown}
        className="relative w-full h-full flex items-center justify-center cursor-pointer focus:outline-2 focus:outline-yellow-400 focus:outline-offset-[-8px]"
      >
        {/* NOW Text - Default view, yellow, bold */}
        <div
          aria-hidden={showingTime}
          className={`
            absolute text-center transition-all duration-700 ease-in-out will-change-[opacity,transform]
            font-bold text-[#FFD400] uppercase tracking-tight
            text-[clamp(4rem,20vw,12rem)]
            ${showingTime ? "opacity-0 scale-105" : "opacity-100 scale-100"}
          `}
        >
          NOW
        </div>

        {/* Time Text - Revealed on interaction, white, medium weight */}
        <div
          aria-hidden={!showingTime}
          className={`
            absolute text-center transition-all duration-700 ease-in-out will-change-[opacity,transform]
            font-medium text-white
            text-[clamp(3rem,15vw,9rem)]
            ${showingTime ? "opacity-100 scale-100" : "opacity-0 scale-95"}
          `}
        >
          {timeString}
        </div>
      </div>
    </main>
  );
}
